.footer__tablet {
    border-bottom: 1px solid #E1E1E1;
    margin-bottom: 12px;

    @include tablet {
        display: flex;
        justify-content: center;
    }
}

.footer__title {
    margin-bottom: 16px;
    width: 232px;
    font-weight: 700;
    font-size: 18px;
    line-height: calc(22/18);
    color: var(--second-color);

    @include tablet {
        width: 352px;
        margin-bottom: 8px;
        font-size: 26px;
        line-height: calc(32/26);
        letter-spacing: 0.04em;
    }

    @include desktop {
        width: 504px;
        margin-bottom: 14px;
        font-weight: 400;
        font-size: 30px;
        line-height: calc(37/30);
        letter-spacing: 0.04em;
    }
}

.footer__text {
    margin-bottom: 60px;
    width: 200px;
    font-weight: 700;
    font-size: 34px;
    line-height: calc(41/34);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: var(--second-color);

    @include tablet {
        width: 352px;
        margin-bottom: 32px;
        font-size: 48px;
        line-height: calc(59/48);
        letter-spacing: 0.06em;
    }

    @include desktop {
        min-width: 504px;
        margin-bottom: 60px;
        font-size: 58px;
        line-height: calc(71/58);
        letter-spacing: 0.06em;
    }
}

.container .footer__social {
   width: 100%;
   padding-right: 0px;
}

.footer__subtitle {
    list-style: none;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 20px;

    @include tablet {
        padding-top: 60px;
        margin-bottom: 34px;
    }

    @include desktop {
        margin-bottom: 40px;
        padding-top: 18px;
    }
}

.footer__item {
    width: 34px;
    height: 34px;
    margin-right: 20px;

    &:nth-child(3) {
        margin-right: 0px;
    }
}

.footer__link {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    border-radius: 50%;
    fill: #84A178;
    background-color: var(--accent-link-color);

    &:hover,
    &:focus {
        fill: var(--second-color);
    }

}

.footer__info {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 12px;
    line-height: calc(22.32/12);
    text-align: right;

    color: var(--additional-color);

    @include tablet {
        margin-bottom: 60px;
        font-size: 14px;
        line-height: calc(26/12);
    }

    @include desktop {
        font-size: 16px;
        line-height: calc(29.76/12);
        letter-spacing: 0.02em;
    }
}

.footer__contact {
    padding-left: 5px;
    font-weight: 700;
    font-size: 12px;
    line-height: calc(22.32/12);
    text-decoration: none;

    color: var(--second-color);
}

.footer__copyright {
    display: block;
    margin: 12px auto;

    max-width: 150px;
    text-align: center;
    font-weight: 700;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    color: #7A7A7A;

    @include tablet {
        max-width: 298px;
    }
}